import * as d3 from 'd3';
import React from 'react';
import { useD3 } from '../../hooks';

export const BarchartD3 = ({ data }) => {
  const ref = useD3(svg => {
    const height = 500;
    const width = 500;
    const margin = { top: 20, right: 30, bottom: 30, left: 40 };

    const x = d3
      .scaleBand()
      .domain(data.map(d => d.client))
      .rangeRound([margin.left, width - margin.right])
      .padding(0.1);

    const y1 = d3
      .scaleLinear()
      .domain([0, d3.max(data, d => d.pas)])
      .rangeRound([height - margin.bottom, margin.top]);
  });

  return (
    <svg
      style={{
        height: 500,
        width: '100%',
        marginRight: '0px',
        marginLeft: '0px',
      }}
    >
      <g className="plot-area" />
      <g className="x-axis" />
      <g className="y-axis" />
    </svg>
  );
};
